import React, { useEffect, useState, useRef } from 'react';
import Toggle from "react-switch"

export default function SoundSwitch(props) {
    useEffect(()=>{
    }, [])
    return (
        <Toggle  onChange={() => props.toggleSound(!props.soundEnabled)} checked={props.soundEnabled} uncheckedHandleIcon={<div>off</div>} checkedHandleIcon={<div>on</div>} uncheckedIcon={false} checkedIcon={false}>

        </Toggle>
    )
}
